main.profile_billing_plans {
    // padding-left: 20px;
    background-color: #E8E9F1;
    display: flex;

    .hero {
        width: 90%;
        // height: 900px;

        .profile_detail {
            background: url("../assets/Profile_adminstration_background.svg") center no-repeat;
            height: 160px;
            padding: 40px;
            position: relative;
            margin-top: -40px;

            .profile_detail_wrapper {
                width: 580px;
                display: flex;
                justify-content: space-between;
                margin: auto;

                figure {
                    margin: auto;
                    margin-top: 120px;
                    padding-left: 240px;
                    width: 210px;
                    position: relative;

                    .profile_image {
                        width: 140px;
                        height: 140px;
                        border-radius: 50%;
                        border: 4px solid #fff;
                    }

                    .profileImageCameraIcon {
                        position: absolute;
                        bottom: 20px;
                        right: 0px;
                        padding: 10px;
                        border-radius: 100%;
                        background: rgba(255, 255, 255, 0.8);

                    }
                }

                .hero_name_detail {
                    margin-top: 210px;
                    margin-right: 50px;

                    h2 {
                        color: #1E2875;
                        padding: 5px 5px;
                        border-radius: 6px;
                        font-size: 20px;
                        font-weight: 600;
                        margin-bottom: -2px;
                        margin-left: 7px;
                        margin-top: 5px;
                        width: 150px;
                    }

                    span {
                        color: #00000080;
                        background: #FFBB0D;
                        ;
                        padding: 5px 2px;
                        border-radius: 6px;
                        font-size: 15px;
                        font-weight: 400;
                        margin-left: 13px;
                    }
                }
            }


            .backgroundThreeDotIcon {
                position: absolute;
                top: 60px;
                right: 30px;
            }

        }

        .profile_wrapper {
            display: flex;
            justify-content: space-between;
            padding: 80px 20px;




            .profile_edit_field {
                width: 672px;
                background-color: #ffffff;
                border-radius: 15px;
                padding: 20px 60px 20px 60px;

                .header_links {
                    margin-bottom: 20px;

                    span {
                        color: #15182E;
                        font-weight: 400;
                        font-size: 16px;
                        opacity: 0.3;
                        padding-bottom: 7px;
                        padding-right: 120px;
                        border-bottom: 2px solid rgba(0, 0, 0, 0.3);
                    }

                    span.active {
                        opacity: 5;
                        border-bottom: 2px solid #1E2875;

                    }
                }

               .plan_wrapper {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                padding-top: 20px;

                .plan_card {
                    width: 280px;
                    background: rgba(244, 244, 248, 0.5);
                    border-radius: 10px;
                    padding: 20px;
                    div  {
                        display: flex;
                        flex-direction: column;
                        margin-bottom: 120px;
                        span.plan_type {
                            color: #15182E;
                            font-size: 20px;
                            font-weight: 500;
                        }

                        span.offer {
                            color: #768389;
                            font-size: 18px;
                            font-weight: 400;
                            margin-top: 12px;
                        }

                        span.amount {
                            color: #15182E;
                            font-size: 32px;
                            font-weight: 500;
                            margin-top: 25px;
                        }
                    }

                    button {
                        padding: 10px 20px;
                        color: #768389;
                        background: rgba(118, 131, 137, 0.5);
                        border-radius: 8px;
                        border: none;
                        font-weight: 500;
                        font-size: 20px;
                        width: 270px;
                    }

                    button.choose_plan {
                        color: #FFFFFF;
                        background: #1E2875;
                    }
                }
               }

                
            }
        }
    }
}